import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { ContactUsComponent } from './components/contact-us/contact-us.component';
import { JoinUsFormComponent } from './components/join-us-form/join-us-form.component';
import { ViewMembersComponent } from './components/view-members/view-members.component';
import { DocumentsComponent } from './components/documents/documents.component';
import { DocumentDetailsComponent } from './components/document-details/document-details.component';


const routes: Routes = [
  {path:'',component:HomeComponent},
  {path:'about-us',component:AboutUsComponent},
  {path:'contact-us',component:ContactUsComponent},
  {path:'join-us',component:JoinUsFormComponent},
  {path:'reports',component:ViewMembersComponent},
  {path:'docs',component:DocumentsComponent},
  {path:'document-details',component:DocumentDetailsComponent},
  {path:'**',component:HomeComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
