import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class JoinusformService {

  constructor(private http:HttpClient) { }
  getInstitutions(){
    return this.http.get('http://localhost:51319/api/institution/Get');
  }
  getOccupations(){
    return this.http.get('http://localhost:51319/api/occupation/Get');
  }
}
