import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MemerServiceService {

  endPoint ='http://localhost:51319';
  constructor(private http:HttpClient) { }
  saveMember(memberData){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
      })
    };
    debugger
    var data ={
      firstName:memberData.firstName,
      surName:memberData.surName,
      idNumber:memberData.idNumber,
      cell:memberData.cell,
      gender:memberData.gender,
      emailAdddress:memberData.emailAdddress,
      termsAndConditions:memberData.termsAndConditions,
      instituion_Id:memberData.institutionId,
      occupation_Id:memberData.occupationId,
      address:{
        city:memberData.city,
        code:memberData.code,
        street:memberData.street,
        province:memberData.province
      }
    }
   return  this.http.post(`${this.endPoint}/api/member/Save`,JSON.stringify(data),httpOptions);
  }
  saveMemberPhoto(photoModel){
    var formdata = new FormData();
    formdata.append("file",photoModel.file);
    return this.http.post(`${this.endPoint}/api/Photo/uploadFile/${photoModel.idNumber}`,formdata);
  }
}
