  ngOnInit() {
  
    this.spinner.show('Please wait.....');
    this.httpService.getInstitutions().subscribe((res:HttpResponse)=>{
      
      if(res.succeeded){
        this.instituions =res.results;
      }
  
    },(error)=>{
   
      this.showFailure=true;
      this.showSuccess=false;
      this.errorMessage="Internal server error ocured please contact administrator.";
      this.spinner.hide();
      
    });

    this.httpService.getOccupations().subscribe((res:HttpResponse)=>{
      
      if(res.succeeded){
        this.spinner.hide();
        this.occupations =res.results;
      }
  
    },(error)=>{
      this.showFailure=true;
      this.showSuccess=false;
      this.errorMessage="Internal server error ocured please contact administrator.";
      this.spinner.hide();
      
    });
  }

  getCities(province){
    this.cities= this.provinces.find(x=>x.name.includes(province.target.value)).cities;
  }

